export default {
	data() {
		return {
			login: false,
			avatarUrl: '/static/logo.png',
			uerInfo: null
		}
	},
	methods: {
		goLogin() {
			if (!this.login) {
				uni.navigateTo({
					url: '/pages/login/login'
				});
			}
		},
		goAbout() {
			uni.navigateTo({
				url: '/pages/about/about'
			});
		},
		gocollect(){
			uni.navigateTo({
				url: '/pages/collect/collect'
			});
		},
		onShow(){
			console.log(this.$loginUrl)
			this.uerInfo=getApp().globalData.userInfo;
			if(this.uerInfo!=null){
				this.login= true
			}
		},
	},

}
